<template>
  <div class="category" v-if="categories">
    <HomeNav
      :titles="titles"
      :gender="gender"
      @navigate-to="navigate"
    ></HomeNav>

    <div class="main">
      <van-sidebar v-model="activeKey">
        <van-sidebar-item
          v-for="(mCate, index) in cates"
          :key="index"
          :title="mCate.name"
        />
      </van-sidebar>

      <ul class="content">
        <van-skeleton
          title
          avatar
          :row="3"
          :loading="loading"
          avatar-shape="square"
        >
          <CateBookItem
            v-for="(book, index) in cateBooks"
            :key="index"
            :book="book"
          ></CateBookItem>
        </van-skeleton>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import HomeNav from "@/components/HomeNav.vue";
import CateBookItem from "@/components/CateBookItem.vue";

export default {
  name: "Category",
  components: {
    HomeNav,
    CateBookItem,
  },
  data() {
    return {
      gender: "male",
      titles: [
        {
          id: 0,
          name: "男生",
          gender: "male",
          path: "/home/?gender=male",
        },
        {
          id: 1,
          name: "女生",
          gender: "female",
          path: "/home/?gender=female",
        },
      ],
      activeKey: 0, //选择的分类
      cateBooks: [],
      loading: true,
    };
  },
  computed: {
    ...mapState(["categories", "maleStorage", "femaleStorage"]),
    cates() {
      if (this.gender == "male") {
        return this.categories.male;
      } else {
        return this.categories.female;
      }
    },
  },
  created() {
    if (this.categories) {
      this.activeKey = this.cates.findIndex((item) => {
        return item.name == this.$route.query.name;
      });
      this.changeCates();
      return;
    }
    this.axios.get("/categories").then((response) => {
      this.$store.commit("updateCategories", response.data);
      this.activeKey = this.cates.findIndex((item) => {
        return item.name == this.$route.query.name;
      });
      this.changeCates();
    });
  },
  watch: {
    "$route.query.gender"() {
      this.activeKey = 0;
      this.changeCates();
    },
    activeKey() {
      this.changeCates();
      if (this.$route.query.name == this.cates[this.activeKey].name) {
        return;
      }

      if (this.$route.query.gender) {
        this.$router.push(
          `/category?name=${this.cates[this.activeKey].name}&gender=${
            this.$route.query.gender
          }`
        );
      } else {
        this.$router.push(`/category?name=${this.cates[this.activeKey].name}`);
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.gender = to.query.gender || "male";
      vm.$emit("update:active", 1);
      console.log("beforeRouteEnter");
    });
  },
  beforeRouteUpdate(to, from, next) {
    // 可以访问组件实例 `this`
    this.gender = to.query.gender || "male";
    this.loading = true;
    console.log("beforeRouteUpdate");
    next();
  },
  methods: {
    navigate(n) {
      if (
        this.$route.query.name == this.cates[this.activeKey].name &&
        this.$route.query.gender == n
      ) {
        return;
      }
      this.$router.push(
        `/category?name=${this.categories[n][0].name}&gender=${n}`
      );
    },
    changeCates() {
      if (this.useStorage()) {
        this.$nextTick(()=>{
          this.loading = false;
        })
        return;
      }

      this.axios
        .get(
          `/category-info?gender=${this.gender}&type=hot&major=${
            this.cates[this.activeKey].name
          }&minor=&start=0&limit=20`
        )
        .then((response) => {
          this.saveStorage(response);
          if (response.data.books.length) {
            this.loading = false;
          }
        });
    },
    // 数据存到vuex中
    saveStorage(response) {
      if (this.gender == "male") {
        if (!this.maleStorage[this.cates[this.activeKey].name]) {
          this.$store.commit("updateMaleStorage", {
            id: this.cates[this.activeKey].name,
            obj: response.data.books,
          });
          this.cateBooks = response.data.books;
        }
      } else {
        if (!this.femaleStorage[this.cates[this.activeKey].name]) {
          this.$store.commit("updateFemaleStorage", {
            id: this.cates[this.activeKey].name,
            obj: response.data.books,
          });
          this.cateBooks = response.data.books;
        }
      }
    },
    useStorage() {
      if (this.gender == "male") {
        if (this.maleStorage[this.cates[this.activeKey].name]) {
          this.cateBooks = this.maleStorage[this.cates[this.activeKey].name];
          return true;
        }
      } else {
        if (this.femaleStorage[this.cates[this.activeKey].name]) {
          this.cateBooks = this.femaleStorage[this.cates[this.activeKey].name];
          return true;
        }
      }
      return false;
    },
  },
};
</script>

<style lang="less" scoped>
.category {
  .van-skeleton {
    padding-top: 15px;
    height: calc(100vh - 90px);
  }
  .main {
    display: flex;
    height: calc(100vh - 90px);
    .van-sidebar {
      background-color: #f7f8fa;
      text-align: center;
    }
    .content {
      flex: 1;
      overflow-y: scroll;
    }
  }
}
</style>