<template>
    <li class="cate-book-item" @click="toDetail">
      <div class="pic">
        <van-image
          lazy-load
          :src="`http://statics.zhuishushenqi.com${book.cover}`"
        />
      </div>
      <div class="info">
        <div class="name">{{ book.title }}</div>
        <div class="desc">{{ book.shortIntro }}</div>
        <div class="bottom">
          <span class="author">{{ book.author }}</span>
          <span class="category">{{ book.majorCate }}</span>
        </div>
      </div>
    </li>
</template>

<script>
export default {
  name: "CateBookItem",
  props: ["book"],
  methods: {
    toDetail() {
      this.$router.push({
        name: "BookDetail",
        query: { id: this.book._id },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.cate-book-item {
  display: flex;
  margin: 10px 0;
  .pic {
    .van-image {
      height: 100%;
      width: 90px;
    }
  }
  .info {
    margin: 0 15px 0 6px;
    .name {
      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1; /*行数*/
      -webkit-box-orient: vertical;
    }
    .desc {
      font-size: 13px;
      color: #999;
      margin: 10px 0;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; /*行数*/
      -webkit-box-orient: vertical;
    }
    .bottom {
      font-size: 12px;
      padding-top: 16px;
      position: relative;
      .author {
        color: #666;
      }
      .category {
        color: #777;
        position: absolute;
        display: inline-block;
        padding: 0 6px;
        background-color: #efeff4;
        border-radius: 8px;
        right: 0;
      }
    }
  }
}
</style>